@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@400;700&family=Glegoo:wght@400;700&family=Lobster+Two:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
/* 
* Fonts used:
* - Cinzel
* - Glegoo
* - Lobster Two
* - Material Icons
*/

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}
body {
	line-height: 1;
}
ol,
ul {
	list-style: none;
}
blockquote,
q {
	quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* My modifications */
* {
	box-sizing: border-box;
	outline: none;
}

a {
	text-decoration: none;
}

button {
	border: none;
	background: none;
	cursor: pointer;
}

:root {
	--background-color: #222831;
	--dark-color: #204051;
	--standard-color: #3b6978;
	--light-color: #fbc687;

	font-size: 62.5%;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

@media (max-width: 700px) {
	:root {
		font-size: 60%;
	}
}

#page-home {
	min-height: 100vh;
	min-width: 100%;
	background-color: var(--background-color);

	box-sizing: border-box;
	overflow: hidden;

	display: flex;
	flex-direction: column;
	align-items: center;

	font-family: 'Glegoo', serif;
	font-size: 1.6rem;
	color: var(--background-color);

	position: relative;
}

main {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	width: 80%;

	padding: 80px 0;
}

.add-book {
	box-sizing: content-box;

	height: 3.5rem;
	overflow: hidden;
	white-space: nowrap;

	color: white;
	font-family: Glegoo, serif;
	font-weight: bold;
	font-size: 3rem;

	border: none;
	background: none;
	cursor: pointer;

	display: flex;
	align-items: center;
	justify-content: center;

	padding-bottom: 0.5rem;
	margin-bottom: 30px;
}

.add-book:hover {
	border-bottom: 0.2rem dashed var(--light-color);
	padding-bottom: 0.3rem;
}

.add-book span {
	font-family: 'Material Icons';
	margin-right: 10px;
}

.cards-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

@media (max-width: 700px) {
	main {
		width: 100%;
		padding: 40px 0 80px;
	}
}

header {
	/* position: absolute;
	top: 0;
	left: 0; */
	width: 100%;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	background-color: var(--background-color);
	border-bottom: 2px solid var(--dark-color);
}

header h1 {
	font-family: 'Lobster Two', cursive;
	font-size: 5rem;
	font-weight: 400;
	color: white;

	line-height: 1.5em;
}

header h2 {
	color: var(--light-color);
	font-size: 1.2rem;
	/* font-style: italic; */
	opacity: 0.8;

	margin: 0.4rem 0 1rem;
}

@media (max-width: 700px) {
	header h2 {
		display: none;
	}
}

.card {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	text-align: center;

	width: 300px;
	height: 200px;
	margin: 20px;

	background-color: var(--light-color);
	border-radius: 4px;
	box-shadow: 0 0 20px 2px #000000c5;
}

.card .card-header h2 {
	margin: 15px 10px 10px;

	font-family: 'Cinzel', serif;
	font-size: 2.4rem;
	font-weight: 400;
	color: var(--background-color);
}

.card .card-header h3 {
	color: var(--background-color);
}

.card .card-footer {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	align-items: center;

	width: 100%;
	padding: 0 10px 8px 8px;
}

.card .card-footer .buttons {
	display: flex;
	text-align: start;
}

.card .card-footer .buttons button {
	font-family: 'Material Icons';
	font-size: 24px;
	color: var(--background-color);
	opacity: 40%;

	border: none;
	background: none;
	cursor: pointer;
}

.card .card-footer .buttons button + button {
	margin-left: 8px;
}

.card .card-footer .buttons button:hover {
	color: rgb(153, 33, 33);
	opacity: 100%;
}

.card .card-footer .buttons button:active {
	color: var(--background-color);
}

.card .card-footer h4 {
	color: var(--background-color);
}

.card .card-footer .checkbox-container {
	display: flex;
	justify-content: flex-end;
}

.card .card-footer .checkbox {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.card .checkbox label {
	font-size: 14px;
	margin-bottom: 5px;
}

#modal {
	background-color: #000000df;

	height: 100vh;
	width: 100vw;

	position: fixed;
	top: 0;
	z-index: 2;

	display: flex;
	justify-content: center;
	align-items: center;

	transition: 600ms;
}

#modal.hide {
	display: none;
}

#modal .content {
	color: white;
	background-color: #222831df;
	padding: 20px;
	border-radius: 8px;

	width: 420px;
}

#modal .header {
	display: flex;
	align-items: center;
	justify-content: space-between;

	margin-bottom: 48px;
}

#modal .header h1 {
	color: white;

	font-size: 26px;
	font-weight: bold;
	line-height: 30px;
}

#modal .header button#btn-modal-close {
	background-image: url(/small-library-react/static/media/x.56cb1449.svg);

	width: 20px;
	height: 20px;

	transform: scale(1.2);
	opacity: 0.6;

	display: flex;

	font-size: 0.01px;
}

#modal .header button#btn-modal-close:hover {
	opacity: 1;

	transition: opacity 300ms;
}

.body {
	display: flex;
	flex-direction: column;

	font-size: 18px;
}

.body label {
	margin-bottom: 8px;
}

.body input {
	margin-bottom: 20px;

	font-family: Calibri;
	font-size: 18px;

	padding: 2px 8px;
}

.body input.required:invalid {
	border: 2px solid rgb(243, 96, 96);
}

.body .lower {
	display: grid;
	grid-template-areas:
		'lbPages lbRead'
		'inPages inRead';
	justify-content: space-between;
	align-items: center;
}

.body .lower .label-pages {
	grid-area: lbPages;
}
.body .lower .input-pages {
	grid-area: inPages;
}
.body .lower .label-read {
	grid-area: lbRead;
}
.body .lower .input-read {
	grid-area: inRead;
	transform: scale(1.5);
	margin-left: 53px;
}

.body button#btn-modal-add {
	display: block;
	width: 100%;
	text-align: center;

	margin-top: 20px;
	padding: 8px;
	border: 2px solid white;
	border-radius: 8px;

	font: inherit;
	font-weight: bold;

	color: white;
}

.body button#btn-modal-add:hover {
	border-color: #fbc687;
	color: #fbc687;

	transition: 300ms;
}

footer {
	position: absolute;
	bottom: 0;
	left: 0;

	width: 100%;

	display: flex;
	justify-content: center;

	background-color: var(--background-color);
	border-top: 2px solid var(--dark-color);
}

footer p {
	font-family: 'Glegoo', serif;
	font-size: 1.6rem;
	color: white;

	padding: 0.5em 0;
	line-height: 2em;
}

footer p a {
	color: lightcoral;
	font-weight: bold;
}

footer p a:hover {
	height: 10px;
	background-color: lightcoral;
	color: white;
}

